body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#app{
  position: relative;
  width: 100vw;
  height: 100vh;
}

h1,h2,h3,h4,h5,h6{
  margin: 0;
}

*{
  box-sizing: border-box;
}

*,
*::after,
*::before,{
  box-sizing: inherit;
}
.Home {
  background: -webkit-gradient(linear, left top, left bottom, from(#50C1E9), to(#3BAEDA));
  background: -webkit-linear-gradient(#50C1E9, #3BAEDA);
  background: linear-gradient(#50C1E9, #3BAEDA);
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}
/*# sourceMappingURL=home-layout.css.map */
.logo {
  font-size: 3em;
  color: #434A54;
}
/*# sourceMappingURL=logo.css.map */
.login {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  color: #434A54;
}

.login form {
  padding: 20px 0;
  display: flex;
  flex-flow: column nowrap;
}

.login form .input {
  width: 250px;
  margin: 5px 0;
  border-radius: 20px;
  background-color: white;
  padding: 5px 15px;
  border: 2px solid #434A54;
}

.login form .input input {
  font-size: 1.3em;
  background: none;
  border: none;
  outline: none;
  color: #434A54;
}

.login form .input input::-webkit-input-placeholder {
  color: #CCD0D9;
}

.login form .input input::-ms-input-placeholder {
  color: #CCD0D9;
}

.login form .input input::placeholder {
  color: #CCD0D9;
}

.login form button {
  margin-top: 10px;
  border-radius: 20px;
  outline: none;
  border: none;
  padding: 5px 0;
  font-size: 1.3em;
  background: -webkit-gradient(linear, left top, left bottom, from(#48CFAE), to(#48CFAE));
  background: -webkit-linear-gradient(#48CFAE, #48CFAE);
  background: linear-gradient(#48CFAE, #48CFAE);
  color: white;
}
/*# sourceMappingURL=login.css.map */
.footer {
  position: fixed;
  bottom: 0;
  padding: 2em;
  background-color: #434A54;
  color: #AAB2BD;
  width: 100%;
  cursor: pointer;
  font-size: .7em;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-transition: .3s;
  transition: .3s;
}

.footer:hover {
  color: white;
}

@media screen and (min-width: 768px) {
  .footer {
    font-size: 1em;
  }
}
/*# sourceMappingURL=footer.css.map */
